@import (less) "color.less";

@fontSizeTitle: 0.875em;

.panel {
    border: 1px !important;
    border-color: @white;
    box-shadow: none;
    .panel-open {
      background-color: @lightest_gray !important;
      border: 1px !important;
      border-color: @gray;
    }
}
.panel-heading {
    border: 1px !important;
    // border-radius: 2px 2px 0px 0px !important;
    box-shadow: none;
    color: @green;
    border-color: @gray;
    background-color: @white;
    padding: 1em 1.250em 1em 0em !important;
    height: 50px;
    width: 18.750em;
    &:hover {
      cursor: pointer;
    }
    // &.panel-heading-round{
    //   border-radius: 25px !important;
    // }
    .panel-title {
        color: green;
        font-weight: bold;
        font-size: 0.750em !important;
        text-transform: uppercase;
        text-align: center;
    }
    .accordion-arrow {
        color: black;
        position: relative;
        font-size: 26px !important;
        margin-top: 1.156em;
    }
    .stringa{
        color: @white;
        font-size: 0.750em !important;
        margin-right: 1.875em;
        font-weight: bold;
        font-family: arial;
    }
    h4 {
        margin: 0;
        font-size: 1em;
    }
}

.panel-body {
    border: 1px !important;
    box-shadow: none;
    background-color: @lightest_gray !important;
    border-radius: 0px 0px 2px 2px !important;
}

.panel-collapse{
    width: 100%;
    .panel-body;
     max-height: 0;
     transition: max-height 0.15s ease-out;
     overflow: hidden;
    &.in{
      max-height: 1000em;
      transition: max-height 0.25s ease-in;
    }
  }

.panelTitle{
      font-size: @fontSizeTitle;
      font-weight: bold;
    //   text-transform: uppercse;
}

.accordionIcon{
     font-size: 32px;
     position:absolute;
     top:9px;
}


.nbp-panel-showmore {
  a {
      text-decoration: none !important;
      margin-top: -3px;
      margin-right: 4.962em;
  }  
  >.panel-heading {
      background-color: @white !important;
      position: relative;
      & .accordion-icon{
          .accordionIcon
      }
      >.panel-title{
       .panelTitle;
        >.title{
          padding: 2px 20px 2px 0px;
        }
        .accordion-title-icon{
          padding: 2px 2px 2px 52px !important;
          margin-bottom:10px;
        }
        text-align: left !important;
    }
    >.panel-open {
      background-color: @lighter_green !important;
    }
  }
}

:host-context(.contrasto-on) {
    .panel {
        .panel-open {
          background-color: @white !important;
          color:@black;
          // border: 1px solid @black !important;
          .panel-title {
            color: @black;
          }
          .stringa {
            color:@black;
          }
          .accordion-arrow {
            color:@black;
          }
        }
    }
    .panel-heading {
        background-color: @white !important;
        // &:hover {
        //   background-color: @white !important;
        //   color: @black;
        //   border: 1px solid @black !important;
        //   .panel-title {
        //     color: @black;
        //   }
        //   .stringa {
        //     color:@black;
        //   }
        //   .accordion-arrow {
        //     color:@black;
        //   }
        // }
        .panel-title {
            color: @black;
        }
        .accordion-arrow {
            color: @black;
        }
        .stringa{
            color: @black;
        }
    }

    .panel-body {
        background-color: @white !important;
        border-top: none !important;
        // border-right: 2px solid @black !important;
        // border-bottom: 2px solid @black !important;
        // border-left: 2px solid @black !important;
        color:@black;
    }
    .accordion-green-header {
        >.panel-heading {
          background-color: @black !important;
          &:hover {
            background-color: @white !important;
            color:@black;
            border: 1px solid @black;
          }
        }
        >.panel-open {
          background-color: @white !important;
          color:@black;
          border:1px solid @black !important;
        }
    }
}
